import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import loading_img from './giphy.gif';
import Gallery from 'react-grid-gallery';


const addr = 'https://home.plawn-inc.science/face//api/find';
const dims = { size: window.innerHeight / 6.2 };
const give_url = e => 'https://cdn.plawn-inc.science/face/' + e;

const give_links = (...ids) => ids.map(e => ({
  src: give_url(e),
  thumbnail: give_url(e),
  thumbnailWidth: dims,
  thumbnailHeight: dims
}));

const make_form_from_obj = obj => {
  const fd = new FormData();
  for (const key in obj) fd.append(key, obj[key]);
  return fd;
}

const Render_loading = props => props.visible ? <div style={{ textAlign: 'center' }}><img src={loading_img}></img></div> : <div></div>

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      images: [],
      file: null,
      loading: false
    }
    this.ask_results = this.ask_results.bind(this);

    this.set_loading = () => this.setState({ loading: true });
    this.unset_loading = () => this.setState({ loading: false });

  }
  ask_results() {
    this.set_loading();
    fetch(addr, {
      method: 'POST',
      body: make_form_from_obj({ file: this.state.file })
    }
    ).then(data => data.json())
      .then(data => {
        this.unset_loading();
        this.setState({ images: give_links(...(data.found.map(e => e.split('/').slice(-1)))) });
      });

  }
  render() {
    return (
      <div>
        <div style={{ textAlign: 'center' }}>
          <h1>Find your Photos</h1>
          <form action="find" method="post" encType="multipart/form-data>">
            <input type="file" name="file" onChange={e => this.setState({ file: e.target.files[0] })}></input>
          </form>
          <input type="button" value="Find pictures" onClick={this.ask_results}></input>
        </div>
        <Render_loading visible={this.state.loading} />
        <Gallery images={this.state.images} />
      </div>
    );
  }
}

export default App;
